import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/page/index.vue'
import UserInfo from '@/views/page/userInfos.vue'
import Payment from '@/views/page/payment.vue'
import Complied from '@/views/page/complied.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Index,
      meta: { title: '首页' }
    },
    {
      path: '/infos',
      name: 'infos',
      component: UserInfo,
      meta: { title: '用户填写' }
    },
    {
      path: '/payment',
      name: 'payment',
      component: Payment,
      meta: { title: '支付确认' }
    },
    {
      path: '/complied',
      name: 'complied',
      component: Complied,
      meta: { title: '支付完成' }
    }
  ]
})



