<template>
  <div>
    <Header></Header>
    <div>
      <div slot='header'>支付页面</div>
      <div slot='content'>
        <p>订单号:{{ order.orderNo }}</p>
        <p>手机号:{{ carded.phone }}</p>
        <p>数量:{{ carded.sum }}</p>
        <p>总金额:{{ carded.price }}</p>
      </div>
      <div>{{ carded }}</div>
      <div slot='footer'>
        <mt-button size="large" type="primary" slot='footer' @click.native='handlePay'>确认支付</mt-button>
      </div>
    </div>
  </div>
</template>

<style lang="" scoped>
</style>

<script>
import Header from '@/container/nav.vue';
import Card from '@/components/card.vue';
import { wxPay } from  '@/utils/weixin.js';
  export default {
    name: 'payment',
    components: { Header, Card },
    data() {
      return {
        pays: {}
      }
    },
    methods: {
      handlePay(){
        this.$indicator.open();
        console.log()
        wxPay(this.$store.state.order.payConfig);
        setTimeout(() => {
          this.$router.push('/complied');
          this.$indicator.close();
        }, 1000);
      }
    },
    computed: {
      carded() {
        return this.$store.state.carded;
      },
      order() {
        return this.$store.state.order;
      }
    },
    mounted: function() {
      // console.log(this.$store.state.order.payConfig)
      // this.pays.mobile = this.$store.state.carded.phone;
      // this.pays.code = this.$store.state.carded.code;
      // this.pays.num = this.$store.state.carded.sum;
    }
  }
</script>



