<template>
  <div>
    <mt-header fixed :title="this.$route.meta.title">
      <router-link v-if="this.$route.path!==this.routes[0]" :to="getNav()" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
    {{this.$route.name}}
  </div>
</template>

<style lang="" scoped>
</style>

<script>
export default {
  data() {
    return {
      routes: ["/", "infos", "payment", "complied"]
    };
  },
  methods: {
    getNav() {
      let previous = '';
      this.routes.find((nav, index) => {
        if(nav === this.$route.name){
          if(nav === '/'){
            previous = this.routes[0];
          }
          previous = this.routes[index-1];
        }
      });
      return previous
    }
  }
};
</script>



