<template>
  <div>
    <Header></Header>
    <div>
      <div>你选择的卡劵为:{{ this.$route.params.data }}</div>
      <div>{{ carded }}</div>
      <div>{{ this.phone.data }}</div>
      <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model.number="phone.data" :state="phone.state"></mt-field>
      <mt-field label="验证码" placeholder="请输入6位数验证码" type="number" v-model.number="code.data" :state="code.state"></mt-field>
      <mt-button size="large" type="primary" @click.native='handleSendCode' plain>获取手机验证码</mt-button>
      <mt-field label="数量" placeholder="请输入数量" type="number" v-model.number="sum.data" :state="sum.state"></mt-field>
    </div>
    <mt-button size="large" type="primary" slot='footer' @click.native='handleSubmit'>确定</mt-button>
  </div>
</template>

<style>
</style>

<script>
import Header from '@/container/nav.vue';
import { senSmsUsingGET, createOrderUsingPOST } from  '@/../client/javascript/vue-api-client.js';
export default {
  name: 'userInfos',
  components: { Header },
  data() {
    return {
      phone: {
        data: null,
        state: ''
      },
      code: {
        data: null,
        state: ''
      },
      sum: {
        data: null,
        state: ''
      }
    };
  },
  methods: {
    handleSendCode() {
      senSmsUsingGET({mobile:this.phone.data}).then(function(res){
        console.log(res);
      })
    },
    handleSubmit() {
      if (
        this.phone.state === 'success' &&
        this.code.state === 'success' &&
        this.sum.state === 'success'
      ) {
        this.$indicator.open();
        //生成订单
        createOrderUsingPOST({
          mobile: this.phone.data,
          code: this.code.data,
          num: this.sum.data,
        }).then((response) => {
          console.log(response);
          if(response.data.code === 0){
            this.$store.commit('createOrder', response.data.body)
          }
        })
        this.$store.commit('buyCard', {
          sum: this.sum.data,
          price: 10,
          phone: this.phone.data,
          code: this.code.data,
          payed: false
        })
        setTimeout(() => {
          this.$router.push('/payment');
          this.$indicator.close();
        }, 2000);
      } else {
        this.$toast('请填写正确');
      }
    }
  },
  computed: {
    carded() {
      return this.$store.state.carded;
    }
  },
  watch: {
    'phone.data': function() {
      const reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
      if (!reg.test(this.phone.data)) {
        this.phone.state = 'error';
      } else {
        this.phone.state = 'success';
      }
    },
    'code.data': function() {
      const reg = 6 && /^\d{6}$/;
      if (!reg.test(this.code.data)) {
        this.code.state = 'error';
      } else {
        this.code.state = 'success';
      }
    },
    'sum.data': function() {
      const reg = /^\d+$/;
      if (!reg.test(this.sum.data)) {
        this.sum.state = 'error';
      } else {
        this.sum.state = 'success';
      }
    },
    deep: true,
  },
  mounted: function() {
    console.log(this.$route.matched);
    console.log(this.$store.state)
    this.phone.data = this.$store.state.carded.phone;
    this.sum.data = this.$store.state.carded.sum;
  }
};
</script>



