<template>
  <div class='warp'>
    <Card>
      <div slot='header' class='card-header'>{{ card.name }}</div>
      <div slot='content' class='card-content'>
        <img src="../../images/product1.jpg" alt="" srcset="">
      </div>
      <div slot='footer'>
        <mt-button size="large" type="primary" slot='footer' @click.native='handleBuy'>购买</mt-button>
      </div>
    </Card>
  </div>
</template>

<style >
.warp {
  display: flex;
  flex-wrap: wrap;
}
.card-content {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  background: #000;
  height: 150px;
}
.card-content img{
  width: 100%;
}
</style>

<script>
import Card from '@/components/card.vue';
export default {
  name: 'product',
  components: { Card },
  data() {
    return {
      card: {
        name: '卡劵1',
      }
    };
  },
  methods: {
    handleBuy(){
      this.$router.push({ name:'infos', params: {data: this.card.name}})
    }
  }
};
</script>



