import axios from 'axios'

let wx = require('weixin-js-sdk')
console.log(wx)

export const wxPay = function (parameters = {}) {
  console.log(parameters)
  wx.config({
    debug: true,
    appId: parameters['appId'], // 必填，公众号的唯一标识
    timestamp: parameters['timeStamp'], // 必填，生成签名的时间戳
    nonceStr: parameters['nonceStr'], // 必填，生成签名的随机串
    signature: parameters['paySign'], // 必填，签名，见附录1
    jsApiList: ['config', 'chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
  })
  wx.chooseWXPay({
    timestamp: parameters['timeStamp'], // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
    nonceStr: parameters['nonceStr'], // 支付签名随机串，不长于 32 位
    package: parameters['package'], // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
    signType: parameters['signType'], // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
    paySign: parameters['paySign'], // 支付签名
    success: function (res) {
      // 支付成功后的回调函数
      console.log(res)
    },
    fail: function (err) {
      console.log(err)
    }
  })
}



