<template>
  <div class='card'>
    <slot class='card-header' name='header'></slot>
    <slot class='card-content' name='content'></slot>
    <slot class='card-footer' name='footer'></slot>
  </div>
</template>

<style lang="" scoped>
  .card{
    width: 100%;
    padding: 8px;
    margin: 10px;
    border: 1px gainsboro solid;
    box-sizing: border-box;
  }
  .card-content{
    overflow: hidden;
  }
</style>

<script>
  export default {
    data() {
      return {

      }
    }
  }
</script>



