import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    carded: {
      name: '1',
      sum: null,
      price: null,
      phone: null,
      code: null,
      payed: false
    },
    order: {}
  },
  mutations: {
    buyCard (state, { name, sum, price, phone, code, payed }) {
      state.carded.name = name
      state.carded.sum = sum
      state.carded.price = price
      state.carded.phone = phone
      state.carded.code = code
      state.carded.payed = payed
    },
    createOrder (state, orders) {
      state.order = orders
    }
  },
  actions: {}
})

export default store



